.shop {
	margin: 110px 0 25px;

	@media (min-width: 576px) {
		margin: 150px 0 25px;
	}

	&__filter {
		margin-top: 50px;
		display: flex;
		justify-content: space-between;

		&-content {
			display: flex;

			@media (max-width: 1200px) {
				display: none;
			}

			.shop__select {
				position: relative;
				margin-right: 12px;

				svg {
					position: absolute;
					right: 17px;
					top: 50%;
					transform: translateY(-50%);
					z-index: 99999;
				}
			}

			.selectric-open+svg {
				transform: rotate(180deg) translateY(50%);

				path {
					stroke: #ffffff
				}
			}

			.selectric-open .selectric {
				background-color: #FF7C2A;

				.label {
					color: #ffffff;
				}
			}

			.selectric {
				width: 156px;
				height: 39px;
				border: 1px solid #FF7C2A;
				box-sizing: border-box;
				border-radius: 166.768px;
				background-color: transparent;

				.label {
					color: #FF7C2A;
					font-size: 16px;
					font-weight: 700;
					text-transform: lowercase;
				}

				.button {
					background-color: transparent;
				}
			}

			.selectric .button:after {
				content: '';
				display: block;
				border: 0;
			}

			.selectric-items li {
				color: #FF7C2A;
				font-size: 16px;
				font-weight: 700;
				text-transform: lowercase;

				&:hover {
					background-color: #ff9351;
					color: #ffffff;
				}

				&:first-child {
					display: none;
				}
			}

			.selectric-items li.highlighted {
				background-color: #ff9351;
				color: #ffffff;
			}

			.selectric-items {
				background: #fff;
				box-shadow: none;
				border: none;
				margin-top: 8px;
				// border-radius: 30px;
				// overflow: hidden;
			}
		}

		&-result {
			display: flex;
			align-items: center;
			margin-top: 21px;

			@media (max-width: 1200px) {
				display: none;
			}

			a {
				font-size: 16px;
				display: inline-block;
				font-weight: 400;
				text-decoration: underline;
				color: #000;

				&:hover {
					text-decoration: none;
				}
			}

			div {
				background: #F3F1F2;
				border-radius: 166.768px;
				padding: 8px 21px;
				height: 39px;
				display: flex;
				align-items: center;
				justify-content: space-between;
				margin-right: 12px;

				p {
					margin-right: 22px;
					// margin: 0;
					font-size: 16px;
					font-weight: 700;
					text-transform: lowercase;
				}

				svg {
					cursor: pointer;
				}
			}
		}

		&-mob {
			display: none;

			@media (max-width: 1200px) {
				display: flex;
				justify-content: space-between;
				width: 100%;
			}

			button:first-child {
				margin-right: 10px;
			}

			button {
				width: 50%;
				height: 30px;
				border: 1px solid #FF7C2A;
				border-radius: 166.768px;
				background-color: transparent;
				color: #FF7C2A;
				font-size: 12px;
				font-weight: 700;

				&:hover {
					background-color: #FF7C2A;
					color: #ffffff;
				}
			}
		}
	}

	&__items {
		margin-top: 60px;
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		grid-gap: 50px;

		@media (min-width: 768px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (min-width: 992px) {
			grid-template-columns: repeat(3, 1fr);
		}

		@media (min-width: 1200px) {
			grid-template-columns: repeat(4, 1fr);
		}

		&.close {
			display: none;

			&.open {
				display: grid;
			}
		}
	}

	&__item {
		text-align: center;

		&:hover {
			.shop-colors {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__img {
		width: 295px;
		height: 170px;

		@media (max-width: 1024px) {
			margin: auto;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&-colors {
		margin-top: 35px;
		opacity: 0;
		visibility: hidden;
		transition: all 0.2s ease;

		&__numbers {
			display: flex;
			justify-content: center;
			color: #000;
			font-size: 20px;

			span {
				&:not(:last-child) {
					margin-right: 15px;
				}
			}
		}

		.color-select__selected {
			display: none;
		}
	}

	&__select {
		&-color {
			.selectric-items li {
				position: relative;
				padding-left: 40px;
			}

			.selectric-items li:before {
				content: '';
				position: absolute;
				top: 50%;
				left: 10px;
				transform: translateY(-50%);
				width: 20px;
				height: 20px;
				border-radius: 50%;
			}

			&--1 {
				&::before {
					background-color: #000;
				}
			}

			&--2 {
				&::before {
					background-color: $color-orange;
				}
			}

			&--3 {
				&::before {
					background-color: $color-red;
				}
			}

			&--4 {
				&::before {
					background-color: $color-beige;
				}
			}

			&--5 {
				&::before {
					background-color: $color-teal;
				}
			}
		}

		&-material {
			.selectric-items {
				min-width: 240px;
			}
		}
	}
}

// modal
.modal-filter {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #ffffff;
	z-index: 999999999;
	padding: 30px 16px;
	padding-right: 0;
	overflow-y: scroll;

	&.active {
		display: block;
	}

	svg {
		position: fixed;
		right: 15px;
		top: 8px;
		cursor: pointer;
		width: 50px;
		height: 50px;
		padding: 18px;
	}

	.modal__lists {
		margin-bottom: 110px;

		.modal__list {
			border-bottom: 1px solid #E8E5E6;
			padding-bottom: 20px;
			padding-top: 27px;

			&:last-child {
				border-bottom: none;
			}

			h2 {
				font-size: 18px;
				font-weight: 700;
				margin-bottom: 16px;
			}

			ul {
				margin: 0;
				padding: 0;
				list-style: none;
			}

			li {
				margin-bottom: 14px;
				font-size: 14px;
				font-weight: 700;
				cursor: pointer;
				display: flex;

				&:hover {
					color: #FF7C2A;
				}

				span {
					width: 15px;
					height: 15px;
					border-radius: 50%;
					margin-right: 9px;
				}
			}
		}
	}

	.modal__bottom {
		border-top: 1px solid #E8E5E6;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		background-color: #ffffff;
		padding: 34px 16px;
		text-align: center;

		.btn {
			width: 100%;
		}

		a {
			display: block;
			margin-top: 15px;
			color: #9B9598;
			font-size: 12px;
			font-weight: 400;
			text-transform: lowercase;
			text-decoration: underline;

			&:hover {
				color: #000;
			}
		}
	}

}

// body,
.scroll-no {
	overflow: hidden;
}

@media (max-width: 1024px) {
	.shop-colors {
		opacity: 1;
		visibility: visible;
	}
}