.intro {
	position: relative;
	background-color: $color-grey;
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 570px;

	@media (min-width: 576px) {
		min-height: 800px;
	}

	&__title {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		width: 100%;
		text-align: center;
		font-size: 64px;
		line-height: 1.2;
		font-weight: 700;
		color: $color-orange;
		letter-spacing: -3px;

		@media (min-width: 576px) {
			font-size: 130px;
		}

		@media (min-width: 992px) {
			font-size: 200px;
		}
	}

	&__img {
		width: 235px;
		height: 470px;

		@media (min-width: 576px) {
			width: 388px;
			height: 770px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}