.attract {
	padding: 0 15px;

	&__swiper {
		padding-top: 130px;
	}

	&__content {
		border-radius: 60px;
	}

	&__slide {
		display: flex;
		align-items: center;
		background-color: $color-orange;
		max-width: 1360px;
		margin: auto;
		border-radius: 60px;

		@media (max-width: 1200px) {
			justify-content: center;
		}

		@media (max-width: 991px) {
			flex-direction: column;
			padding: 40px 0 15px;
		}
	}

	&__title {
		max-width: 565px;
		font-size: 120px;
		font-weight: 700;
		letter-spacing: -2px;
		color: #fff;
		transform: translate(0px, -120%);
		opacity: 0;
		transition: all 0.8s ease 0s;

		@media (max-width: 1200px) {
			font-size: 90px;
			text-align: center;
		}

		@media (max-width: 575px) {
			font-size: 48px;
			text-align: left;
			max-width: 300px;
			padding: 0 15px;
		}
	}

	&__right {
		max-width: 565px;
		transform: translate(0px, 120%);
		opacity: 0;
		transition: all 0.8s ease 0s;

		@media (max-width: 1360px) {
			padding: 0 15px;
		}

		@media (max-width: 991px) {
			text-align: center;
			transform: translate(0px, -120%);
		}
	}

	&__img {
		@media (min-width: 992px) {
			padding-right: 30px;
		}

		@media (min-width: 1360px) {
			padding-right: 85px;
		}

		@media (max-width: 1200px) {
			width: 430px;
			height: 430px;
		}

		@media (max-width: 991px) {
			order: 1;
			margin-top: 60px;
		}

		@media (max-width: 575px) {
			width: auto;
			height: 235px;
		}

		img {
			@media (max-width: 1200px) {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__text {
		margin-bottom: 40px;
		font-size: 14px;
		line-height: 1.5;
		text-transform: lowercase;
		color: #fff;
	}

	@media (min-width: 576px) {
		&__text {
			font-size: 20px;
		}
	}
}

.swiper-slide-active .attract__title,
.swiper-slide-active .attract__right {
	transform: translate(0px, 0px);
	opacity: 1;
}