.header {
	padding-top: 31px;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;

	&__content {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__logo {
		position: relative;
		z-index: 6;

		img {
			@media (max-width: 480px) {
				width: 180px;
			}
		}
	}

	&__right {
		display: flex;
		align-items: center;
		position: relative;
		z-index: 5;
	}
}

.header-lang {
	position: relative;
	margin-left: 27px;
	background-color: $color-teal;
	border-radius: 100px;
	cursor: pointer;
	width: 53px;
	height: 21px;

	@media (max-width: 991px) {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		margin: 75px 0 0 0;
		text-align: left;
	}

	&::before {
		content: "";
		right: 7px;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		background-image: url('../img/arrow-down-1.svg');
		background-size: 100%;
		background-position: center;
		background-repeat: no-repeat;
		width: 8px;
		height: 4px;
		pointer-events: none;
		z-index: 1;
	}

	&__text {
		position: relative;
		z-index: 1;
		color: #fff;
		font-size: 14px;
		font-weight: 700;
		padding-left: 8px;
		height: 100%;
		padding-top: 1px;
	}

	&__list {
		position: absolute;
		top: 0;
		opacity: 0;
		visibility: visible;
		transition: all 0.1s linear;
		width: 100%;
		background-color: $color-teal;
	}

	&.is-active &__list {
		opacity: 1;
		visibility: visible;
		top: 13px;

		&:last-child {
			border-radius: 0 0 15px 15px;
		}
	}

	&.is-active &__item {
		&:first-child {
			padding-top: 5px;
		}

		&:last-child {
			border-radius: 0 0 15px 15px;
			padding-bottom: 5px;
		}
	}

	&__link {
		display: block;
		color: #fff;
		line-height: 24px;
		font-size: 14px;
		font-weight: 700;
		padding-left: 8px;
		transition: all 0.3s ease;
		width: 100%;

		&:hover {
			color: $color-orange;
		}
	}

	&-footer {
		top: 80px;
		left: initial;
		right: 0;
		transform: none;
		margin: 0;

		@media (min-width: 992px) {
			display: none;
		}

		@media (max-width: 480px) {
			top: 65px;
		}
	}
}

.menu {
	&__body {
		@media (max-width: 991px) {
			background-color: $color-orange;
			position: fixed;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 130px 20px 50px;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;

			&:before {
				content: "";
				background-color: $color-orange;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 85px;
				z-index: 50;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}


	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		li {
			a {
				position: relative;
				font-size: 20px;
				font-weight: 700;
				text-transform: lowercase;
				transition: all 0.3s ease;

				&:hover {
					color: #000 !important;
				}

				&.active {
					&::before {
						content: '';
						position: absolute;
						bottom: -13px;
						left: 50%;
						transform: translateX(-50%);
						width: 5px;
						height: 5px;
						border-radius: 50%;

						@media (max-width: 991px) {
							background-color: #fff !important;
						}
					}
				}

				@media (max-width: 1200px) {
					font-size: 18px;
				}

				@media (max-width: 991px) {
					color: #fff !important;
					font-size: 28px;
				}
			}

			&:not(:last-child) {
				margin-right: 34px;

				@media (max-width: 1200px) {
					margin-right: 15px;
				}

				@media (max-width: 991px) {
					margin: 0 0 30px 0;
				}
			}
		}

		li:nth-child(1) a {
			color: $color-beige;

			&.active {
				&::before {
					background-color: $color-beige;
				}
			}
		}

		li:nth-child(2) a {
			color: $color-teal;

			&.active {
				&::before {
					background-color: $color-teal;
				}
			}
		}

		li:nth-child(3) a {
			color: $color-red;

			&.active {
				&::before {
					background-color: $color-red;
				}
			}
		}

		li:nth-child(4) a {
			color: $color-orange;

			&.active {
				&::before {
					background-color: $color-orange;
				}
			}
		}

		li:nth-child(5) a {
			color: $color-beige;

			&.active {
				&::before {
					background-color: $color-beige;
				}
			}
		}

		li:nth-child(6) a {
			color: $color-red;

			&.active {
				&::before {
					background-color: $color-red;
				}
			}
		}
	}
}

@media (max-width: 991px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 50px;
		height: 50px;
		cursor: pointer;
	}

	.menu__icon::before,
	.menu__icon::after {
		position: absolute;
		left: 17px;
		height: 2px;
		width: 18px;
		transition: all 0.3s ease 0s;
		background-color: #000;
		border-radius: 50%;
		border: 1px solid #000;
	}

	.menu__icon.active:before,
	.menu__icon.active:after {
		border-radius: 0;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 20px;
	}

	.menu__icon::after {
		bottom: 20px;
	}

	.menu__icon.active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
	}

	.menu__icon.active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
	}
}