.country {
	margin-top: 60px;

	@media (min-width: 992px) {
		margin-top: 140px;
	}

	&__title {
		font-size: 36px;
		font-weight: 700;
		text-transform: lowercase;
		text-align: center;

		@media (max-width: 992px) {
			margin-bottom: 30px;
		}
	}

	&__name {
		font-size: 48px;
		font-weight: 700;
		line-height: 1.3;
		letter-spacing: -3px;
		padding-left: 15px;
		padding-right: 15px;

		@media (min-width: 992px) {
			font-size: 120px;
			padding-left: 40px;
			padding-right: 40px;
		}
	}

	&__btn {
		margin-top: 70px;
		text-align: center;

		@media (max-width: 991px) {
			margin-top: 40px;
		}
	}
}

.eocjs-newsticker .eocjs-newsticker-container {
	position: relative;
	min-height: 65px;

	@media (min-width: 992px) {
		min-height: 130px;
	}
}

.eocjs-newsticker .eocjs-newsticker-one,
.eocjs-newsticker .eocjs-newsticker-two {
	position: absolute;
	left: 0;
	top: 0;
	white-space: nowrap;
}