.why {
	margin-top: 60px;

	@media (min-width: 768px) {
		margin-top: 85px;
	}

	&__item {
		display: flex;
		align-items: center;
		background-color: #fff;
		padding: 0 15px;
		max-width: 1360px;
		margin: auto;

		@media (max-width: 575px) {
			flex-direction: column;
		}
	}

	&__text {
		opacity: 0;
		visibility: hidden;
		transition: all 0.8s ease 0s;

		@media (max-width: 575px) {
			width: 100%;
		}
	}

	&__title {
		font-size: 120px;
		font-weight: 700;
		letter-spacing: -3px;
		line-height: calc(100 / 120);
		text-transform: lowercase;

		@media (max-width: 991px) {
			font-size: 75px;
		}

		@media (max-width: 575px) {
			font-size: 48px;
		}
	}

	&__subtitle {
		margin-top: 60px;
		font-size: 36px;
		font-weight: 700;
		text-transform: lowercase;

		@media (max-width: 991px) {
			margin-top: 30px;
			font-size: 18px;
		}
	}

	&__desc {
		font-size: 28px;
		margin-top: 28px;
		max-width: 610px;
		line-height: calc(32 / 28);

		@media (max-width: 991px) {
			margin-top: 10px;
			font-size: 14px;
			max-width: 100%;
		}
	}

	&__img {
		padding-left: 20px;
		margin: auto;
		opacity: 0;
		visibility: hidden;
		transform: translate(0px, 120%);
		transition: all 0.8s ease 0s;

		@media (max-width: 575px) {
			order: -1;
			margin-bottom: 30px;
			height: 300px;
			padding-left: 0;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
}

.swiper-slide-active .why__img,
.swiper-slide-active .why__text {
	transform: translate(0px, 0px);
	opacity: 1;
	visibility: visible;
}