.be-bigger {
	position: relative;

	@media (min-width: 992px) {
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 50vw;
			height: 100%;
			background-color: #f3f1f2;
			z-index: -1;
		}
	}

	&__slide {
		display: flex;
		height: 100%;
		max-width: 1360px;
		margin: auto;

		@media (max-width: 991px) {
			flex-direction: column;
			align-items: center;
			text-align: center;
		}

		@media (min-width: 992px) {
			height: 100vh;
			padding: 0 15px;
		}
	}

	&__left {
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 100%;

		@media (max-width: 1200px) {
			align-items: center;
		}

		@media (max-width: 991px) {
			height: 375px;
			padding-top: 40px;
			background-color: #f3f1f2;
		}

		@media (min-width: 992px) {
			margin-right: 15px;
		}

		@media (min-width: 1360px) {
			margin-right: 85px;
		}
	}

	&__info {
		max-width: 330px;

		@media (max-width: 575px) {
			padding: 0 15px;
		}

		@media (min-width: 1200px) {
			max-width: 565px;
		}
	}

	&__title {
		font-size: 64px;
		line-height: calc(121 / 120);
		font-weight: 700;
		letter-spacing: -2px;

		@media (min-width: 1200px) {
			font-size: 120px;
		}
	}

	&__desc {
		font-size: 14px;
		line-height: calc(30 / 20);
		text-transform: lowercase;
		margin-top: 30px;

		@media (min-width: 992px) {
			font-size: 20px;
		}
	}

	&__btn {
		margin-top: 25px;

		@media (min-width: 992px) {
			margin-top: 42px;
		}

		&.btn {
			padding: 15px 118px;

			@media (min-width: 992px) {
				padding: 22px 118px;
			}
		}
	}

	&__right {
		display: flex;
		align-items: center;
		transform: translate(0px, 120%);
		opacity: 0;
		transition: all 0.8s ease 0s;

		@media (max-width: 991px) {
			margin-top: 25px;
			flex-direction: column;
			padding: 0 15px;
		}
	}

	&__img {
		@media (max-width: 1200px) {
			width: 450px;
			height: 450px;
		}

		@media (max-width: 991px) {
			width: auto;
			height: 300px;
		}

		img {
			border-radius: 60px;

			@media (max-width: 1200px) {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}

.swiper-slide-active .be-bigger__right {
	transform: translate(0px, 0px);
	opacity: 1;
}