.contacts {
	margin: 110px 0 25px;

	@media (min-width: 576px) {
		margin: 150px 0 25px;
	}

	&__content {
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__title {
		@media (min-width: 992px) {
			width: 57%;
		}
	}

	&__right {
		@media (max-width: 991px) {
			margin-top: 30px;
		}

		@media (min-width: 992px) {
			width: 43%;
		}
	}

	&__subtitle {
		font-weight: 700;
		font-size: 20px;
		line-height: 1.5;

		@media (min-width: 576px) {
			font-size: 44px;
		}
	}

	&__address {
		font-size: 14px;
		line-height: 1.5;
		margin-top: 30px;

		@media (min-width: 576px) {
			font-size: 20px;
		}

		address {
			font-style: normal;
		}

		a {
			display: inline-block;
			color: #000;

			&:hover {
				text-decoration: underline;
			}
		}
	}

	&__mail {
		display: inline-block;
		font-weight: 700;
		font-size: 14px;
		line-height: 1.5;
		color: $color-orange;
		margin-top: 10px;

		@media (min-width: 576px) {
			font-size: 26px;
		}

		&:hover {
			text-decoration: underline;
		}
	}

	&__desc {
		font-weight: 700;
		font-size: 14px;
		line-height: 1.5;
		margin-top: 40px;

		@media (min-width: 576px) {
			font-size: 26px;
			margin-top: 85px;
		}
	}

	&__form {
		margin-top: 20px;

		@media (min-width: 576px) {
			margin-top: 40px;
		}

		input {
			border-bottom: 1px solid #E8E5E6;
			font-size: 14px;
			line-height: 1.5;
			color: #000;
			text-transform: lowercase;
			padding-bottom: 10px;

			@media (min-width: 576px) {
				font-size: 20px;
			}

			&:not(:last-child) {
				margin-top: 40px;
			}

			&::-moz-placeholder {
				opacity: 1;
				color: #000;
			}

			&:-ms-input-placeholder {
				opacity: 1;
				color: #000;
			}

			&::-webkit-input-placeholder {
				opacity: 1;
				color: #000;
			}

			&::-ms-input-placeholder {
				opacity: 1;
				color: #000;
			}

			&::placeholder {
				opacity: 1;
				color: #000;
			}
		}

		.btn {
			width: 100%;
			margin-top: 50px;
		}

	}
}