.weare {
	padding: 0 15px;

	&__swiper {
		padding-top: 85px;
	}

	&__slide {
		background-color: #ff7c2a;
		border-radius: 60px;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
		flex-direction: column;
		padding: 40px 20px;
		min-height: 475px;
		max-width: 1360px;
		margin: auto;

		@media (min-width: 576px) {
			min-height: 630px;
			padding: 40px 85px;
		}

		@media (min-width: 768px) {
			align-items: center;
		}

		@media (min-width: 1200px) {
			flex-direction: row;
			padding: 0 85px;
		}
	}

	&__title {
		font-size: 48px;
		font-weight: 700;
		letter-spacing: -3px;
		text-transform: lowercase;
		color: #fff;
		transform: translate(0px, -120%);
		opacity: 0;
		transition: all 0.8s ease 0s;

		@media (min-width: 576px) {
			font-size: 75px;
		}

		@media (min-width: 768px) {
			font-size: 95px;
		}

		@media (min-width: 1315px) {
			font-size: 120px;
		}
	}

	&__img {
		@media (max-width: 1200px) {
			margin-top: 50px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__left {
		padding-right: 10px;
		transform: translate(0px, -120%);
		opacity: 0;
		transition: all 0.8s ease 0s;

		@media (min-width: 1200px) {
			transform: translate(0px, 120%);
		}

		@media (max-width: 1200px) {
			padding-right: 0;
			width: 100%;
		}
	}

	&__text {
		margin-bottom: 40px;
		font-size: 20px;
		line-height: 1.5;
		text-transform: lowercase;
		color: #fff;

		@media (max-width: 1200px) {
			text-align: center;
		}

		@media (max-width: 575px) {
			text-align: left;
			font-size: 14px;
		}

		p+p {
			margin-top: 15px;
		}
	}

	&__btn {
		@media (max-width: 1200px) {
			text-align: center;
		}

		.btn {
			@media (max-width: 575px) {
				width: 100%;
			}
		}
	}
}

.swiper-slide-active .weare__title,
.swiper-slide-active .weare__left {
	transform: translate(0px, 0px);
	opacity: 1;
}